
import {resume} from '@/assets/resume'

export default {
  data () {
    return {
      resume,
      swiperOptions: {
        simulateTouch: false,
        pagination: {
          el: '.swiper-pagination',
          type: 'bullets',
          clickable: true // 允许点击小圆点跳转
        },
        navigation: {
          clickable: true, // 允许点击小圆点跳转
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        },
        mousewheel: true
      }
    }
  },
  computed: {
    swiper () {
      return this.$refs.criSwiper.$swiper
    }
  },
  mounted () {
    // 页面挂载
    // console.log(this.resume)
    // console.log('Current Swiper instance object', this.swiper)
  },
  methods: {
    pre: function (value) {
      if (!value) return ''
      value = value.toString()
      return value.replace(/\n/g, '<br>')
    },
    slideChangeTransitionStart () {
      let index = this.swiper.activeIndex
      if (index > 3 + resume.experience.length && index < resume.project.length + resume.experience.length + 4) {
        // console.log('picCarousel' + index)
        setTimeout(() => {
          // console.log(this.$refs[`picCarousel${index}`])
          this.$refs[`picCarousel${index}`][0].setActiveItem(0)
        }, 300)
      }
    }
  },
  filters: {

  }
}



