<template>
  <div class="container">

    <swiper
      ref="criSwiper"
      :mousewheel="true"
      :options="swiperOptions"
      @slideChangeTransitionStart="slideChangeTransitionStart">
      <!-- slide-start -->
      <swiper-slide>
        <div class="part_o">
          <img src="../../assets/img/view/title.png" width="100%">
        </div>
      </swiper-slide>
      <!-- slide-end -->
      <!-- slide-start -->
      <swiper-slide>
        <div class="par_tw">
          <h1>求职意向：</h1>
          <div class="line"></div>
          <div class="title cri cri-d-1">
            <img src="../../assets/img/view/intention.png" width="100%;">
          </div>
        </div>
      </swiper-slide>
      <!-- slide-end -->
      <!-- slide-start -->
      <swiper-slide>
        <div class="par_th">
          <h1>基本信息：</h1>
          <div class="line"></div>
          <div class="info">
            <div class="lef cri cri-r-1">
              <i>姓 名：</i><i>{{resume.basic.name}}</i><br>
              <i>年 龄：</i><i>{{resume.basic.age}}</i><br>
              <i>籍 贯：</i><i>{{resume.basic.birthplace}}</i><br>
              <i>Q Q：</i><i>{{resume.basic.qq}}</i><br>
              <i>手 机：</i><i>{{resume.basic.phone}}</i><br>
            </div>
            <div class="rig cri cri-l-1">
              <i>性 别：</i><i>{{resume.basic.gender}}</i><br>
              <i>民 族：</i><i>{{resume.basic.national}}</i><br>
              <!--<i>学 历：</i><i>{{resume.basic.education}}</i><br>-->
              <!--<i>毕业学校：</i><i>{{resume.basic.graduation}}</i><br>-->
              <i>现居地：</i><i>{{resume.basic.nowIn}}</i><br>
              <i>邮 箱：</i><i class="unline">{{resume.basic.email}}</i><br>
              <i>微 信：</i><i>{{resume.basic.wechat}}</i><br>
            </div>
          </div>
        </div>
      </swiper-slide>
      <!-- slide-end -->
      <!-- slide-start -->
      <swiper-slide>
        <div class="par_fo">
          <h1>专业技能：</h1>
          <div class="line"></div>
          <div class="info cri cri-u-2">
            <el-scrollbar style="height:100%">
              <div>
                <p v-for="(item, index) in resume.skills" :key="index">
                  {{'· ' + item}}
                </p>
              </div>
            </el-scrollbar>
          </div>
        </div>
      </swiper-slide>
      <!-- slide-end -->
      <!-- slide-start -->
      <swiper-slide v-for="(item, index) in resume.experience" :key="'experience' + index">
        <div class="par_fo">
          <h1>工作经历：</h1>
          <div class="line"></div>
          <div class="info">
            <div class="lef cri cri-d-1" style="width:40%"  @mousewheel.stop>
              <el-scrollbar style="height:100%">
                <div>
                  <p>
                    {{item.name}} <i>（{{item.time}}）</i>
                  </p>
                  <p>
                    部门：{{item.branch}}
                  </p>
                  <p>
                    职位：{{item.position}}
                  </p>
                  <p>
                    工作内容：
                  </p>
                  <p v-html="pre(item.jobDuties)"></p>
                </div>
              </el-scrollbar>
            </div>
            <div class="rig cri cri-d-1 logo" style="width:60%">
              <img :src="item.logo" width="76%">
            </div>
          </div>
        </div>
      </swiper-slide>
      <!-- slide-end -->
      <!-- slide-start -->
      <swiper-slide v-for="(item, index) in resume.project" :key="'project' + index">
        <div class="par_fo">
          <h1>项目经历：</h1>
          <div class="line"></div>
          <div class="info flex">
            <div class="lef cri cri-r-1" style="width:40%"  @mousewheel.stop>
              <el-scrollbar style="height:100%">
                <div>
                  <!--{{'picCarousel' + (index + 4 + resume.experience.length)}}-->
                  <p>
                    {{item.name}} <i>（{{item.time}}）</i>
                  </p>
                  <p>
                    项目描述：
                  </p>
                  <p v-html="pre(item.projectDes)"></p>
                  <p>
                    责任描述：
                  </p>
                  <p v-html="pre(item.responsibilityDes)"></p>
                </div>
              </el-scrollbar>
            </div>
            <div class="rig flex flex_center cri cri-l-1" style="width:60%">
              <el-carousel
                :ref="'picCarousel' + (index + 4 + resume.experience.length)"
                v-if="item.picture"
                :trigger="'click'"
                :autoplay="false">
                <el-carousel-item v-for="(list, j) in item.picture" :key="j">
                  <div class="pic" :style="{backgroundImage : 'url('+ list +')'}"></div>
                </el-carousel-item>
              </el-carousel>
            </div>
          </div>
        </div>
      </swiper-slide>
      <!-- slide-end -->
      <!-- slide-start -->
      <swiper-slide>
        <div class="par_fo bg-tra flex flex_center flex_col">
          <img class="cri cri-d-1" src="../../assets/img/view/thanks.png" width="60%">
          <img class="cri cri-2" src="../../assets/img/view/line.png" width="100%" style="margin:4% 0">
          <div class="flex fome">
            <div class="flex flex_center flex_col cri cri-u-3">
              <img src="../../assets/img/view/phone.png">
              <div class="til">手机</div>
              <div>{{resume.basic.phone}}</div>
            </div>
            <div class="flex flex_center flex_col cri cri-u-4">
              <img src="../../assets/img/view/email.png">
              <div class="til">邮箱</div>
              <div>{{resume.basic.email}}</div>
            </div>
            <div class="flex flex_center flex_col cri cri-u-5">
              <img src="../../assets/img/view/qq.png">
              <div class="til">QQ</div>
              <div>{{resume.basic.qq}}</div>
            </div>
          </div>
        </div>
      </swiper-slide>
      <!-- slide-end -->
      <div class="swiper-button-prev" slot="button-prev"></div><!--左箭头-->
      <div class="swiper-button-next" slot="button-next"></div><!--右箭头-->
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>

  </div>
</template>

<script>

import controller from './index.js'
export default controller

</script>

<style lang="less" scoped>
  @import "./index.less";
</style>



